}
.backNav:hover {
  -webkit-animation: backandforth .8s ease-in-out infinite;
          animation: backandforth .8s ease-in-out infinite;

}

.refreshNav {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 3em;
  background-image: url(/static/media/refresh.d972f2d3.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin: 1em;
  width: 2em;
  height: 2em;
}
.refreshNav:hover {
  -webkit-animation: roll .5s linear infinite;
          animation: roll .5s linear infinite;
}

@-webkit-keyframes roll {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes roll {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@-webkit-keyframes backandforth {
  0% {
    left: 2.5em;
  }
  50% {
    left: 2.3em;
  }
  100% {
    left: 2.5em;
  }
}

@keyframes backandforth {
  0% {
    left: 2.5em;
  }
  50% {
    left: 2.3em;
  }
  100% {
    left: 2.5em;
  }
}

