/*
 * ________________________________________Switch
 */
.switch {
  /* width was 60px */
  width: 5em;
  height: 34px;
  position: relative;
  display: inline-block;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  border-radius: 34px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  border-radius: 50%;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: var(--switch);
  transition: 0.4s;
}
input:checked {
  background-color: var(--switch);
}
input:focus {
  box-shadow: 0 0 1px var(--switch);
}
input:checked + .slider:before {
  /* was translateX(26px) */
  -webkit-transform: translateX(46px);
  transform: translateX(46px);
  background-color: var(--switch);
}

